.page-footer {
  background-color: $white;
}

.social__item {
  margin: 0 auto;
  margin-top: 33px;
  margin-bottom: 45px;
  padding: 0;
  width: 151px;
  display: flex;
  justify-content: space-between;
}
.social__list {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 47px;
  height: 48px;
  background-color: $light-blue;
}
.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  b {
    margin-right: 8px;
  }
}
@media (min-width: $tablet-width) {
  .copyright {
    justify-content: end;
  }
  .copyright__text {
    display: inline-block;
  }
  .contact {
    text-align: start;
  }
}
