.main-nav {
  background-color: $light-blue;
  position: relative;
  @media (min-width: $tablet-width) {
    background-color: $white;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  min-height: 56px;
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    background-color: $white;
    padding-top: 16px;
    width: 723px;
  }

  @media (min-width: $desktop-width) {
    width: 1069px;
  }
}
.main-nav--closed .main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 12px;
  width: 23px;
  height: 22px;
  background-color: $light-blue;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: #668ec0;
  }
  &:active {
    background-color: #5496bd;
    opacity: 70%;
  }
  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -1px;
    width: 25px;
    height: 5px;
    background-color: #ffffff;
    box-shadow: 0 9px 0 0 #ffffff;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--open .main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  width: 22px;
  height: 22px;
  background-color: $light-blue;
  border: none;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 9px;
    right: 0;
    width: 22px;
    height: 3px;
    background-color: $white;
  }
  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: $white;
    opacity: 0.5;
  }
  @media (min-width: $tablet-width) {
    display: none;
  }
}
