.intro {
  margin: 0 auto;
}

.intro__logo {
  height: 390px;
  text-align: center;
  background-image: url(../img/prague-city-1.jpg);
  background-position: center;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    height: auto;
  }

  @media (min-width: $desktop-width) {
    background-size: cover;
  }

  img {
    width: 282px;
    margin-top: 90px;

    @media (min-width: $tablet-width) {
      width: 380px;
      margin-top: 77px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 104px;
      margin-bottom: 60px;
    }
  }
}

.intro__slogan-text {
  height: 118px;
  margin: 0;
  padding-top: 82px;
  text-align: center;
  font-size: 31px;
  font-weight: 400;
  background-color: $white;

  @media (min-width: $tablet-width) {
    br {
      display: none;
    }
  }
}

.intro__white-shape {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    height: 60px;
    width: 100%;
    background-image: url(../svg/intro-triangle.svg.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
