.page-header {
  background-color: $white;
  display: flex;
  flex-direction: column-reverse;
}

.page-header__logo {
  height: 56px;
  text-align: center;
  cursor: pointer;

  img {
    width: 137px;
    height: 68px;

    @media (min-width: $desktop-width) {
      width: 186px;
      height: 93px;
      z-index: 100;
    }
  }

  @media (min-width: $tablet-width) {
    position: absolute;
    z-index: 100;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.sticky {
  position: sticky;
  z-index: 1000;
  top: 0;
  left: 0;
}
