.site-list__item--map {
  @media (min-width: $tablet-width) {
    justify-self: center;
  }
}

.site-list__item--feedback {
  @media (min-width: $tablet-width) {
    justify-self: end;
  }
}

.site-list a {
  text-decoration: none;
  color: $black;

  @media (max-width: $mobile-width-only) {
    color: $white;
    font-size: 18px;
    font-weight: 700;
    line-height: 40px;
    display: block;
    text-align: center;
    height: 55px;
    padding-top: 5px;
    border-top: 2px solid rgba(255, 255, 255, 0.2);
    border-bottom: 2px solid rgba(1, 1, 1, 0.2);
  }

}

.site-list__item a {

  &:hover {
    color: #5496bd;
  }

  &:active {
    opacity: 20%;
  }

}

@media (max-width: $mobile-width-only) {
  .site-list__item--feedback a {
    border-bottom: none;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 7px;
      width: 100%;
      background: linear-gradient(180deg,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.2) 100%);
    }
  }
}

@media (max-width: $mobile-width-only) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .site-list {
    display: inline;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .site-list__item {
  display: block;
}
