/* play-regular - latin */
@font-face {
  font-family: "Play";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/play-v12-latin-regular.woff2") format("woff2"), url("../fonts/play-v12-latin-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* play-700 - latin */
@font-face {
  font-family: "Play";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/play-v12-latin-700.woff2") format("woff2"), url("../fonts/play-v12-latin-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
li,
a {
  list-style: none;
  text-decoration: none;
}

.page {
  height: 100%;
  font-family: "Play";
  line-height: 26px;
  text-transform: uppercase;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  background-color: #eeeeee;
  margin: 0 auto;
}

.page-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column-reverse;
}

.page-header__logo {
  height: 56px;
  text-align: center;
  cursor: pointer;
}
.page-header__logo img {
  width: 137px;
  height: 68px;
}
@media (min-width: 1200px) {
  .page-header__logo img {
    width: 186px;
    height: 93px;
    z-index: 100;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    position: absolute;
    z-index: 100;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.sticky {
  position: sticky;
  z-index: 1000;
  top: 0;
  left: 0;
}

.intro-wrapper {
  margin: 0 auto;
  width: auto;
  padding: 0 10px;
}

.benefits-wrapper {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  padding-bottom: 30px;
}
@media (min-width: 768px) {
  .benefits-wrapper {
    margin: 0;
    padding-top: 20px;
    min-height: 330px;
    flex-direction: column-reverse;
  }
}
@media (min-width: 1200px) {
  .benefits-wrapper {
    display: grid;
    grid-template-rows: 1fr 1fr;
    justify-content: stretch;
  }
}

.advantages-tablet-wrapper {
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages-tablet-wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 30px;
    padding-right: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.form-wrapper {
  text-align: center;
  padding-top: 33px;
}
@media (min-width: 768px) {
  .form-wrapper {
    padding-top: 56px;
  }
}

.footer-wrapper {
  text-align: center;
  padding-top: 38px;
  padding-bottom: 26px;
  margin-left: 40px;
  margin-right: 40px;
}
@media (min-width: 768px) {
  .footer-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
  }
}

.main-nav {
  background-color: #81B3D3;
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  min-height: 56px;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    background-color: #ffffff;
    padding-top: 16px;
    width: 723px;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 1069px;
  }
}

.main-nav--closed .main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 12px;
  width: 23px;
  height: 22px;
  background-color: #81B3D3;
  border: none;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle:hover {
  background-color: #668ec0;
}
.main-nav--closed .main-nav__toggle:active {
  background-color: #5496bd;
  opacity: 70%;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  left: -1px;
  width: 25px;
  height: 5px;
  background-color: #ffffff;
  box-shadow: 0 9px 0 0 #ffffff;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}

.main-nav--open .main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  width: 22px;
  height: 22px;
  background-color: #81B3D3;
  border: none;
  cursor: pointer;
}
.main-nav--open .main-nav__toggle::before, .main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 22px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--open .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--open .main-nav__toggle:active::before, .main-nav--open .main-nav__toggle:active::after {
  background-color: #ffffff;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .main-nav--open .main-nav__toggle {
    display: none;
  }
}

@media (min-width: 768px) {
  .site-list__item--map {
    justify-self: center;
  }
}

@media (min-width: 768px) {
  .site-list__item--feedback {
    justify-self: end;
  }
}

.site-list a {
  text-decoration: none;
  color: #000000;
}
@media (max-width: 767px) {
  .site-list a {
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    line-height: 40px;
    display: block;
    text-align: center;
    height: 55px;
    padding-top: 5px;
    border-top: 2px solid rgba(255, 255, 255, 0.2);
    border-bottom: 2px solid rgba(1, 1, 1, 0.2);
  }
}

.site-list__item a:hover {
  color: #5496bd;
}
.site-list__item a:active {
  opacity: 20%;
}

@media (max-width: 767px) {
  .site-list__item--feedback a {
    border-bottom: none;
  }
  .site-list__item--feedback a::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 7px;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .site-list {
    display: inline;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .site-list__item {
  display: block;
}

