.intro-wrapper {
  margin: 0 auto;
  width: auto;
  padding: 0 10px;
}
.benefits-wrapper {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  padding-bottom: 30px;
  @media (min-width: $tablet-width) {
    margin: 0;
    padding-top: 20px;
    min-height: 330px;
    flex-direction: column-reverse;
  }
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-rows: 1fr 1fr;
    justify-content: stretch;
  }
}
.advantages-tablet-wrapper {
  background-color: $grey;
  @media (min-width: $tablet-width) {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 30px;
    padding-right: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.form-wrapper {
  text-align: center;
  padding-top: 33px;
  @media (min-width: $tablet-width) {
    padding-top: 56px;
  }
}
.footer-wrapper {
  text-align: center;
  padding-top: 38px;
  padding-bottom: 26px;
  margin-left: 40px;
  margin-right: 40px;
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
  }
}
