.benefits {
  margin: 0 auto;
  background-color: $white;
}

.benefits__list {
  display: grid;
  justify-content: center;
  padding: 0;
  @media (min-width: $tablet-width) {
    margin: 0;
    margin-bottom: 56px;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.benefits__title {
  padding-top: 45px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: left;
  @media (min-width: $tablet-width) {
    text-align: center;
    margin: 0;
    padding: 0;
  }
}
.benefits__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-align: left;
}
.benefits__list-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0;
  padding-right: 12px;
  padding-bottom: 12px;
  @media (min-width: $tablet-width) {
    margin: 0;
    display: grid;
    grid-template-columns: min-content min-content min-content;
    margin-top: 60px;
    margin-bottom: 26px;
    @media (min-width: $desktop-width) {
      order: -1;
    }
  }
  .house {
    width: 39px;
    height: 36px;
    @media (min-width: $tablet-width) {
      width: 75px;
      height: 72px;
    }
  }
  .burger {
    .img {
      margin: 0;
    }
    width: 40px;
    height: 34px;
    @media (min-width: $tablet-width) {
      width: 76px;
      height: 67px;
    }
  }
  .gif {
    width: 32px;
    height: 38px;
    @media (min-width: $tablet-width) {
      width: 64px;
      height: 75px;
    }
  }
}

