.advantages__list {
  margin: 0;
  padding: 0;
  background-color: $light-blue;
}

.advantages__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  margin: 0;
  padding-top: 27px;
}

.advantages__number {
  text-align: center;
  padding-top: 21px;
  padding-bottom: 21px;
}
.advatnages__description {
  text-align: center;
}
.block-pic-1 {
  height: 260px;
  width: auto;
  background-image: url(../img/prague-block1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.advantages__item--pic1 {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}
.block-pic-2 {
  height: 260px;
  width: auto;
  background-image: url(../img/prague-block2.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: $tablet-width) {
    order: -1;
  }
}
.advantages__item--pic2 {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
@media (max-width: $mobile-width-only) {
  .vector-line {
    margin: 0 auto;
    width: 280px;
    height: 1px;
    background-color: #d6d6d6;
  }
}
